import React, { useEffect, useState } from "react";
import { Table, Select, Input, Button, Space } from "antd";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import MonthSelect from "./Select";
import axios from "axios";
const { Option } = Select;

const MonthlyTable = () => {
  const [data, setData] = useState([]);
  const [name, setName] = useState("");
  const [selectedMonth, setSelectedMonth] = useState(null);
  const [totalAmount, setTotalAmount] = useState("");
  const [autoSelectMonth, setAutoSelectMonth] = useState(false);
  const [amountAdded, setAmountAdded] = useState(false);
  const [editingKey, setEditingKey] = useState("");

  const apiBaseUrl = "https://e6e0aab3493c18b5.mokky.dev/free-credit";

  const columns = [
    {
      title: "№",
      dataIndex: "index",
      key: "index",
      render: (text, record, index) => index + 1,
    },
    {
      title: "Имя",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Месяц",
      dataIndex: "month",
      key: "month",
    },
    {
      title: "Сумма",
      dataIndex: "amount",
      key: "amount",
    },
    {
      title: "Действие",
      key: "action",
      render: (text, record) => (
        <Space size="middle">
          <a onClick={() => handleEditRow(record)}>
            <EditOutlined />
          </a>
          <a onClick={() => handleDeleteRow(record.id)}>
            <DeleteOutlined style={{ color: "red" }} />
          </a>
        </Space>
      ),
    },
  ];

  const handleAddRow = async () => {
    if (editingKey !== "") {
      // Завершить редактирование существующей строки
      setEditingKey("");
      return;
    }

    if (name && (totalAmount || amountAdded)) {
      const monthToAdd = autoSelectMonth
        ? getNextMonth(data[data.length - 1]?.month || "Январь")
        : selectedMonth;

      // Подготовка данных для API
      const newData = {
        name,
        month: monthToAdd,
        amount: totalAmount,
      };

      try {
        // Вызов API
        const response = await fetch(
          "https://e6e0aab3493c18b5.mokky.dev/free-credit",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(newData),
          }
        );

        if (!response.ok) {
          // Обработка ошибки от API
          console.error("Не удалось добавить данные через API");
          return;
        }

        // Если вызов API успешен, обновляем состояние новыми данными
        const responseData = await response.json();

        setData([
          ...data,
          {
            id: responseData.id,
            name,
            month: monthToAdd,
            amount: totalAmount,
          },
        ]);

        setName("");
        if (!amountAdded) {
          setAmountAdded(true);
        }
        if (!autoSelectMonth) {
          setAutoSelectMonth(true);
        }
      } catch (error) {
        // Обработка других ошибок, которые могут возникнуть во время вызова API
        console.error("Ошибка при вызове API:", error);
      }
    }
  };

  const handleEditRow = (record) => {
    console.log(record.id);
    setEditingKey(record.id);
    setName(record.name);
    setSelectedMonth(record.month);
    setTotalAmount(record.amount);
  };
  const handleSaveRow = async (record) => {
    try {
      const updatedData = {
        name,
        month: selectedMonth,
        amount: totalAmount,
      };

      const response = await axios.patch(
        `${apiBaseUrl}/${editingKey}`,
        updatedData
      );

      if (response.status !== 200) {
        console.error("Не удалось обновить данные через API");
        return;
      }

      setData((prevData) => {
        const updatedItemIndex = prevData.findIndex(
          (item) => item.id === editingKey
        );
        const updatedItems = [...prevData];
        updatedItems[updatedItemIndex] = {
          ...updatedItems[updatedItemIndex],
          name,
          month: selectedMonth,
          amount: totalAmount,
        };
        return updatedItems;
      });

      setEditingKey("");
      setName(""); // Очищаем имя после завершения редактирования
      setSelectedMonth(null); // Сбрасываем выбранный месяц после завершения редактирования
      setTotalAmount("");
    } catch (error) {
      console.error("Ошибка при вызове API для обновления данных:", error);
    }
  };

  const handleDeleteRow = async (id) => {
    console.log(id);
    try {
      // Вызов API для удаления записи
      const response = await fetch(
        `https://e6e0aab3493c18b5.mokky.dev/free-credit/${id}`,
        {
          method: "DELETE",
        }
      );

      if (!response.ok) {
        // Обработка ошибки от API
        console.error(`Не удалось удалить запись с ключом ${id} через API`);
        return;
      }

      // Если API вызов успешен, обновляем состояние, исключив удаленную запись
      const newData = data.filter((item) => item.id !== id);
      setData(newData);
      setEditingKey("");
    } catch (error) {
      // Обработка других ошибок, которые могут возникнуть во время вызова API
      console.error("Ошибка при удалении записи через API:", error);
    }
  };

  const getNextMonth = (currentMonth) => {
    const months = [
      "Январь",
      "Февраль",
      "Март",
      "Апрель",
      "Май",
      "Июнь",
      "Июль",
      "Август",
      "Сентябрь",
      "Октябрь",
      "Ноябрь",
      "Декабрь",
    ];
    const currentIndex = months.indexOf(currentMonth);
    const nextIndex = (currentIndex + 1) % months.length;
    return months[nextIndex];
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(apiBaseUrl);
        setData(response.data);
        console.log(response.data);
      } catch (error) {
        console.error("Ошибка при получении данных:", error);
      }
    };

    fetchData();
  }, [apiBaseUrl]);

  return (
    <div>
      <Space>
  <Input
    placeholder="Имя"
    value={name}
    onChange={(e) => setName(e.target.value)}
  />
  {data.length === 0 && (
    <>
      {autoSelectMonth ? (
        <span>
          {data.length > 0
            ? getNextMonth(data[data.length - 1]?.month || "Январь")
            : "Январь"}
        </span>
      ) : (
        <MonthSelect
          value={selectedMonth}
          onChange={(value) => setSelectedMonth(value)}
        />
      )}
      {!amountAdded && (
        <Input
          placeholder="Сумма"
          value={totalAmount}
          onChange={(e) => setTotalAmount(e.target.value)}
        />
      )}
    </>
  )}
  <Button
    type="primary"
    onClick={editingKey !== "" ? handleSaveRow : handleAddRow}
  >
    {editingKey !== "" ? "Завершить редактирование" : "Добавить"}
  </Button>
</Space>

      <Table
        columns={columns}
        dataSource={data}
        pagination={false}
        rowKey={(record) => record.id}
        rowClassName={(record) =>
          record.key === editingKey ? "editing-row" : ""
        }
      />
    </div>
  );
};

export default MonthlyTable;
